import React,{useState} from 'react'
const User =(props)=>{
	const[Index,setIndex]=useState(0);

	const nextUser=()=>{
  setIndex(Index+1);
}
	return(
		<div>
		<img src={props.users[Index].avatar_url} /><br />
		<h2>{props.users[Index].login}</h2>
		<button style={{background:'green',width:'100px',height:'40px'}} onClick={nextUser}>next</button>
     

		</div>
		)
}


export default User