import React ,{useState} from 'react';
import logo from './logo.svg';
import './App.css';
import User from './User'
import axios from 'axios'

function App() {
const[Users,setUsers]=useState([]);
const [Loaded,setLoaded]=useState(false)

const loadData=()=>{
  axios.get("https://api.github.com/users")
  .then(res=>{setUsers(res.data);setLoaded(true)});
  /* fetch('https://api.github.com/users')
  .then(res=>res.json())
  .then(data=>{setUsers(data); setLoaded(true)})*/
}


  return (
    <div className="App">
     <button style={btn} onClick={loadData}>Load data</button>
     {Loaded?<User users={Users} />:<h2>not loaded</h2>}
     
    </div>
  );
}

const btn={
  width:'200px',
  height:'50px',
  border:'none',
  background:'green',
  color:'white',
  margin:'10px',
  cursor:'pointer',
  fontSize:'20px'
}
export default App;
